.container {
  background-color: #fff;
  max-width: 740px;
  min-width: 320px;
  overflow: hidden;
  padding: 0 14px 14px;
  width: 100%; }

@media (min-width: 1041px) {
  .container {
    max-width: 768px;
    outline: 0;
    padding: 0 28px 14px; } }

.top-gap-big,
.form .top-gap-big {
  margin-top: 32px; }

.top-gap,
.form .top-gap {
  margin-top: 14px; }

.top-gap-0,
.form .top-gap-0 {
  margin-top: 0; }

.flex-left,
.flex-center,
.flex-right,
.flex-top,
.flex-middle,
.flex-bottom,
.flex-vertical {
  display: flex;
  flex-flow: row nowrap; }

.flex-left,
.flex-center,
.flex-right,
.flex-top,
.flex-middle,
.flex-bottom,
.flex-vertical.flex-left,
.flex-vertical.flex-center,
.flex-vertical.flex-right,
.flex-vertical.flex-top,
.flex-vertical.flex-middle,
.flex-vertical.flex-bottom {
  align-items: stretch;
  justify-content: flex-start; }

.flex-center,
.flex-vertical.flex-middle {
  justify-content: center; }

.flex-right,
.flex-vertical.flex-bottom {
  justify-content: flex-end; }

.flex-top,
.flex-vertical.flex-left {
  align-items: flex-start; }

.flex-middle,
.flex-vertical.flex-center {
  align-items: center; }

.flex-bottom,
.flex-vertical.flex-right {
