.units-gap {
  margin-left: -7px;
  margin-right: -7px; }
  .units-gap > .unit,
  .units-gap > .unit-0,
  .units-gap > .unit-1-2,
  .units-gap > .unit-1-3,
  .units-gap > .unit-2-3,
  .units-gap > .unit-1-4,
  .units-gap > .unit-3-4 {
    padding-left: 7px;
    padding-right: 7px; }

.unit {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%; }

.unit-1-2,
.unit-1-3,
.unit-2-3,
.unit-1-4,
.unit-3-4 {
  flex-shrink: 0; }

.unit-1-2 {
  flex-basis: 50%;
  max-width: 50%; }

.unit-1-3 {
  flex-basis: 33.33%;
  max-width: 33.33%; }

.unit-2-3 {
  flex-basis: 66.67%;
  max-width: 66.67%; }

.unit-1-4 {
  flex-basis: 25%;
  max-width: 25%; }

.unit-3-4 {
  flex-basis: 75%;
  max-width: 75%; }

.flex-vertical {
  flex-direction: column; }
  .flex-vertical > .unit,
  .flex-vertical > .unit-0,
  .flex-vertical > .unit-1-2,
  .flex-vertical > .unit-1-3,
  .flex-vertical > .unit-2-3,
  .flex-vertical > .unit-1-4,
  .flex-vertical > .unit-3-4 {
    max-width: none; }
  .flex-vertical > .unit-1-2 {
    max-height: 50%; }
  .flex-vertical > .unit-1-3 {
    max-height: 33.33%; }
  .flex-vertical > .unit-2-3 {
    max-height: 66.67%; }
  .flex-vertical > .unit-1-4 {
    max-height: 25%; }
  .flex-vertical > .unit-3-4 {
    max-height: 75%; }

.table {
  background-color: #fff;
  border: 0;
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%; }
  .table caption {
    caption-side: bottom;
    color: #777;
    padding: 5px;
    text-align: left; }
  .table th,
  .table td {
    border: 0;
    border-bottom: 1px solid #ddd;
    padding: 5px;
    text-align: left; }
  .table th {
    background-color: rgba(0, 0, 0, 0.05);
    font-weight: 600; }

.form {
  margin: 0; }
